* {
    cursor: none;
    
} 


.cursor-dot,
.cursor-dot-outline,
.cursor-dot-outline-hover {
    pointer-events: none;   
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 999999;
}

.cursor-dot {
    size: 8px;
    width: 8px;
    height: 8px;
    background-color: black;
}

.cursor-dot-outline {
    size: 40px;
    width: 40px;
    height: 40px;
    background-color: rgba(126, 126, 126, 0.3) ;
    border: solid 1px rgb(0, 0, 0);
    transition: 0.2s;
}
.cursor-dot-outline-hover {
    size: 80px;
    width: 80px;
    height: 80px;
    background-color: rgba(255, 78, 226, 0.3) ;
    border: solid 1px rgb(252, 74, 228);
    transition: 0.2s;
}


